<template>
  <section class="requisites">
    <!-- title -->
    <h1 class="requisites__title page__title">Реквизиты компании</h1>
    <!-- content -->
    <div class="requisites-content page-wrapper">
      <div class="requisites-fields">
        <div class="requisites-fields-row fields-row">
          <base-input
            :id="'requisitesCompany'"
            :label="'Компания'"
            v-model="company"
          />
        </div>
        <div class="requisites-fields-row fields-row _inn">
          <base-input :id="'requisitesInn'" :label="'ИНН'" v-model="inn" />
        </div>
        <div class="requisites-fields-row fields-row">
          <base-input
            :id="'requisitesAddress'"
            :label="'Адрес'"
            v-model="address"
          />
        </div>
        <div class="requisites-fields-row fields-row">
          <base-input :id="'requisitesCity'" :label="'Город'" v-model="city" />
        </div>
        <div class="requisites-fields-row fields-row">
          <base-input
            :id="'requisitesCountry'"
            :label="'Страна'"
            v-model="country"
          />
        </div>
        <div class="requisites-fields-row fields-row _index">
          <base-input
            :id="'requisitesIndex'"
            :label="'Индекс'"
            :short="true"
            v-model="index"
          />
        </div>
        <div class="requisites-fields-row fields-row">
          <base-input
            :id="'requisitesPhone'"
            :label="'Телефон'"
            v-model="phone"
          />
        </div>
        <div class="requisites-fields-row fields-row _email">
          <base-input
            :id="'requisitesEmail'"
            :label="'Электронная почта'"
            v-model="endCustomer"
          />
        </div>
        <div class="requisites-fields-row fields-row">
          <base-input :id="'requisitesBio'" :label="'ФИО'" v-model="bio" />
        </div>
        <div class="requisites-fields-row fields-row _position">
          <base-input
            :id="'requisitesPosition'"
            :label="'Должность'"
            v-model="position"
          />
        </div>
        <div class="requisites-file _logo">
          <h5 class="requisites-file__title">Логотип</h5>
          <div
            class="requisites-file-field"
            v-if="!logo"
            @dragover.prevent
            @drop="drop('logo', 'requisitesLogo')"
          >
            <label for="requisitesLogo">
              {{ !$isMobile ? "Перетащите или " : "" }}
              <span>{{ !$isMobile ? "выберите" : "Выберите файл" }}</span>
              {{ !$isMobile ? "файл" : "" }}
              <input
                type="file"
                id="requisitesLogo"
                ref="requisitesLogo"
                multiple
                @change="uploadImage('logo', 'requisitesLogo')"
                accept=".jpg,.jpeg,.png"
              />
            </label>
          </div>
          <div class="requisites-file-loaded" v-else>
            <div class="requisites-file-loaded__close" @click="logo = null">
              <img src="@/assets/icons/clear.svg" alt="Remove" />
            </div>
            <img :src="logo" alt="Logo" />
          </div>
        </div>
        <div class="requisites-file">
          <h5 class="requisites-file__title">Оттиск печати</h5>
          <div
            class="requisites-file-field"
            v-if="!print"
            @dragover.prevent
            @drop.prevent="drop('print', 'requisitesPrint')"
          >
            <label for="requisitesPrint">
              {{ !$isMobile ? "Перетащите или " : "" }}
              <span>{{ !$isMobile ? "выберите" : "Выберите файл" }}</span>
              {{ !$isMobile ? "файл" : "" }}
              <input
                type="file"
                id="requisitesPrint"
                ref="requisitesPrint"
                multiple
                @change="uploadImage('print', 'requisitesPrint')"
                accept=".pdf,.jpg,.jpeg,.png"
              />
            </label>
          </div>
          <div class="requisites-file-loaded" v-else>
            <div class="requisites-file-loaded__close" @click="print = null">
              <img src="@/assets/icons/clear.svg" alt="Remove" />
            </div>
            <img :src="print" alt="Print" />
          </div>
        </div>
      </div>
    </div>
    <!-- buttons -->
    <div class="requisites-buttons page-buttons _save">
      <div class="requisites__send _save__button">
        <base-button :type="'filled'" @click="send"> Сохранить </base-button>
      </div>
    </div>
    <!-- search -->
    <div class="requisites-search page-wrapper">
      <div class="requisites-search-field">
        <base-input
          :id="'requisitesSearch'"
          :placeholder="!$isMobile ? 'Поиск по контрагенту или ИНН' : 'Поиск'"
          :search="true"
          :searchIcon="true"
          :suggestionWidth="380"
          :suggestions="suggestions"
          @setSuggestion="(value) => (requisite = value)"
          @clear="requisite = ''"
          v-model="requisite"
        />
      </div>
    </div>
    <!-- table -->
    <div class="page-wrapper">
      <company-requisites-table :requisites="requisites" />
    </div>
  </section>
  <base-modal
    :open="openModal"
    :text="'Отправлено'"
    @close="openModal = false"
  />
</template>

<script>
import BaseInput from "@/components/common/Base.Input.vue";
import BaseButton from "@/components/common/Base.Button.vue";
import BaseModal from "@/components/common/Base.Modal.vue";
import CompanyRequisitesTable from "@/components/companyRequisites/CompanyRequisitesTable.vue";

export default {
  name: "CompanyRequisites",
  components: {
    BaseInput,
    BaseButton,
    BaseModal,
    CompanyRequisitesTable,
  },
  data() {
    return {
      company: "",
      inn: "",
      address: "",
      city: "",
      country: "",
      index: "",
      phone: "",
      endCustomer: "",
      bio: "",
      position: "",
      requisite: "",
      logo: null,
      print: null,
      openModal: false,
      requisites: [],
      suggestions: [
        "8/21-ES",
        "7/21-ES",
        "6/21-ES",
        "5/21-ES",
        "5/21-ES",
        "5/21-ES",
        "5/21-ES",
        "5/21-ES",
      ],
    };
  },
  mounted() {
    this.getCompanies();
  },
  methods: {
    send() {
      this.openModal = true;
    },
    uploadImage(type, field) {
      const file = this.$refs[field].files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (e) => {
        console.log(type);
        type === "logo"
          ? (this.logo = e.target.result)
          : (this.print = e.target.result);
      };
    },
    drop(type, field) {
      const e = window.event;
      e.preventDefault();
      this.$refs[field].files = e.dataTransfer.files;
      this.uploadImage(type, field);
    },
    getCompanies() {
      this.$api.get(`${process.env.VUE_APP_API}/company`).then((res) => {
        for (let c of res.data.results) {
          const obj = {
            id: c.id,
            name: c.name,
            inn: c.inn,
            address: `${c.address_street && c.address_street + ","} ${
              c.address_city && c.address_city + ","
            } ${c.address_country && c.address_country + ","} ${
              c.address_index && c.address_index
            }`,
            number: `+${c.phone_number}`,
            email: c.email,
            logo: c.logo,
            blankPhoto: c.blank_photo,
          };
          this.requisites.push(obj);
        }
      });
    },
  },
};
</script>

